import React, { Component } from 'react'
import Button from './Button'

export default class ButtonBox extends Component {
  render() {
    return (
      <div className='ButtonBox'>
            <div className="Buttons d-flex flex-row">
                <Button onClick={this.props.onClick} button="AC" value="clear"/>
                <Button onClick={this.props.onClick} button="/" value="/"/>
                <Button onClick={this.props.onClick} button="^" value="^"/>
                <Button onClick={this.props.onClick} button="x" value="*"/>
            {/* </div> */}
            {/* <div className="d-flex flex-row"> */}
                <Button onClick={this.props.onClick} button="7" value="7"/>
                <Button onClick={this.props.onClick} button="8" value="8"/>
                <Button onClick={this.props.onClick} button="9" value="9"/>
                <Button onClick={this.props.onClick} size="2" button="-" value="-"/>
            {/* </div> */}
            {/* <div className="d-flex flex-row"> */}
                <Button onClick={this.props.onClick} button="4" value="4"/>
                <Button onClick={this.props.onClick} button="5" value="5"/>
                <Button onClick={this.props.onClick} button="6" value="6"/>
                <Button onClick={this.props.onClick} size="2" button="+" value="+"/>
            {/* </div> */}
            {/* <div className="d-flex flex-row"> */}
                <Button onClick={this.props.onClick} button="1" value="1"/>
                <Button onClick={this.props.onClick} button="2" value="2"/>
                <Button onClick={this.props.onClick} button="3" value="3"/>
                <Button onClick={this.props.onClick} button="%" value="%"/>
            {/* </div> */}
            {/* <div className="d-flex flex-row"> */}
                <Button onClick={this.props.onClick} button="0" value="0"/>
                <Button onClick={this.props.onClick} button="," value=","/>
                <Button onClick={this.props.onClick} button="." value="."/>
                <Button onClick={this.props.onClick} size="2" button="=" value="equal"/>
            </div>
      </div>
    )
  }
}
