import './App.css';
import ButtonBox from './components/ButtonBox';
import Screen from './components/Screen';
import update from 'immutability-helper';
import * as math from 'mathjs';
import React, { Component } from 'react';

class App extends Component {

  constructor(){
    super();
    this.state = {
      operations : []
    }
  }

  calculateOperations = () =>{
    let result = this.state.operations.join('')
    if(result){
      result = math.evaluate(result)
      result = math.format( result, {precision:14})
      result = String(result)
      this.setState({
        operations: [result],
      });
    }
  }

  handleClick = (e)=>{
    const value = e.target.getAttribute('data-value');
    switch(value){
      case 'clear': this.setState ({
        operations : []
      })
      break;
      case 'equal' : this.calculateOperations() //Function will be called
      break;
      default : 
      const newOprations = update( this.state.operations, {
        $push: [value],
      });
      this.setState({
        operations: newOprations,
      });
      break;
  }
  }

  render() {
    return (
      <div>
      <div className="Wrapper">
      <Screen data={this.state.operations}/>
      <div>
      <ButtonBox onClick={this.handleClick}/>
      </div>
      </div>
      </div>
    )
  }
}

export default App;
