import React, { Component } from 'react'

export default class Button extends Component {


  render() {
    return (
      <div className="Button">
        <button onClick={this.props.onClick} data-size={this.props.size} type="button" class="btn btn-secondary" data-value={this.props.value}>{this.props.button}</button>
      </div>
    )
  }
}
